import * as React from 'react';
import styles from './weekSkin.module.scss';
import WeekSkinTitle from "../weekSkinTitle";
import {useState} from "react";
import weekData from "../../assets/data/weekData";
import WeekList from "../weekList";
function WeekSkin() {
  const [weekSkinList, setWeekSkinList] = useState(weekData);
  return (
    <section className={styles.weekSkin}>
      <WeekSkinTitle/>
      <div className={styles.WeekListContainer}>
        {
          weekSkinList.map((item,) => <WeekList key={item.no}  {...item} />)
        }
      </div>
    </section>
  )
}

export default WeekSkin;