import * as React from 'react';
import styles from './bottomPrecautions.module.scss'

function BottomPrecautions() {
  return (
    <section className={styles.PrecautionsWrap}>
      <div className={styles.Precautions}>
        <h3>별 수호자 PC방 이벤트 유의사항</h3>
        <h4>1. 플레이타임 이벤트</h4>
        <ul>
          <li>플레이타임 이벤트 참여 시간은 PC방에서 로그인한 시간을 기준으로 하며, 로그아웃 시까지의 접속 시간이 기록됩니다.</li>
          <li>이벤트 참여 시간은 서버 동기화 상황에 따라 최대 20분 정도 소요될 수 있습니다.</li>
          <li>접속 직후 접속을 종료하는 경우, 정상적으로 접속 시간이 기록되지 않을 수 있습니다. 최소 5분 이상 접속을 부탁 드립니다.</li>
          <li>획득한 상품은 제작소에서 확인하실 수 있습니다.</li>
          <li>상품 수령은 2022.08.09 (화) 23:59 (KST)까지만 가능하며, 이후에는 이벤트 페이지가 비활성화되어 상품 수령이 불가하오니<span>반드시 이벤트 종료 전 수령을 부탁 드립니다.</span>
          </li>
        </ul>
        <h4>2. 스킨 체험 이벤트</h4>
        <ul>
          <li>스킨 체험 이벤트는 매 주차 시작일의 10:00부터 체험이 가능합니다.
            <span>ex) 1주차 이벤트 스킨의 경우, 2022.07.01 (금) 10:00 (KST)부터 체험 가능</span>
          </li>
          <li>이전 주차 스킨이 적용되고 있는 경우, 재접속 후 확인을 부탁 드립니다.</li>
          <li> 주차별 체험 가능한 스킨은 아래와 같습니다.
            <span> 1주차: 별 수호자 럭스, 별 수호자 뽀삐, 별 수호자 잔나, 별 수호자 룰루</span>
            <span> 2주차: 별 수호자 징크스, 별 수호자 소라카, 별 수호자 신드라</span>
            <span> 3주차: 별 수호자 아리, 별 수호자 이즈리얼, 별 수호자 미스 포츈</span>
            <span> 4주차: 별 수호자 자야, 별 수호자 라칸, 별 수호자 니코, 별 수호자 조이</span>
          </li>
        </ul>


        <div>
          * 라이엇 PC방 가맹점이 아닌 PC방의 경우 이벤트 참여가 불가능할 수 있습니다.<br/>
          * 이벤트에 대한 문의사항은 라이엇 고객지원을 통해 문의하실 수 있습니다.
        </div>
      </div>
    </section>
  )
}

export default BottomPrecautions;