import http from "./http";
import ResponseBase from "../interfaces/httpInterfaces/responseBase";
import {RewardBodyInterface, RewardItemInterface} from "../interfaces/reward.interface";
import {PlayTimeInterface} from "../interfaces/playTime.interface";


class ApiHttp extends http {

  /**
   * QA 상품 지급 처리
   * @param obj : {uuid: string, id: string} ex) "uuid": "742500ed-4d42-5dba-88bf-9a7047c84ddb","itemId": "E01","E02","E03"
   *
   */
  postReward = async (obj: RewardBodyInterface) => {
    try {
      const {data} = await this.axios.post<ResponseBase<RewardItemInterface>>(this.urlPath + '/reward', obj);
      return data;
    } catch (error) {
      return this.respondError<any>(error);
    }
  }
  /**
   * QA 계정 플레이 시간 내역 조회
   * @param uuid : riot game uuid ex) 742500ed-4d42-5dba-88bf-9a7047c84ddb
   */
  getPlayerPlayTime = async (uuid: string) => {
    try {
      const {data} = await this.axios.get<ResponseBase<PlayTimeInterface>>(this.urlPath + `/pcb_sessions/player?uuid=${uuid}`)
      return data;
    } catch (error) {
      return this.respondError<any>(error);
    }
  }
}

const apiHttp: ApiHttp = new ApiHttp();
export default apiHttp;
