import * as React from 'react';
import styles from './playtime.module.scss';
import PlayTimeTitle from "../playTimeTitle";
import PlayTimeItems from "../playTimeItems";
import PlayTimeBar from "../playTimeBar";
import {useEffect, useState} from "react";
import playData from "../../assets/data/playData";
import auth from "../../httpModules/auth";
import {hasParamLoadingManage} from "../../uitls/loadingManage";
import {PlayTimeInterface} from "../../interfaces/playTime.interface";
import apiHttp from "../../httpModules/apiHttp";
import LoadingModal from "../loadingModal";
import moment from "moment";
import {PlayDataInterface, widthInterface} from "../../interfaces/useState.interface";
import ErrorModal from "../errorModal";
import {errorStore} from "../../store/errorStore";
import {milliSecondsToNumber} from "../../uitls/unitChange";
import FORMAT from "../../consts/format";

const init: PlayTimeInterface = {
  time: "00:00:00",
  milliSeconds: 0,
  items: []
}

function PlayTime() {
  const isAuth = auth.isAuth;

  const [playGift, setPlayGift] = useState<PlayDataInterface[]>(playData);
  const getPlayTime = hasParamLoadingManage<PlayTimeInterface, string>(apiHttp.getPlayerPlayTime, "742500ed-4d42-5dba-88bf-9a7047c84ddb");

  const [playTimeData, setPlayTimeData] = useState<PlayTimeInterface>(init);
  const [timePercent, setTimePercent] = useState<number>(0);
  const [progressWidth, setProgressWidth] = useState<widthInterface>({width: '0'});
  const [realTime, setRealTime] = useState<string>(moment().format(FORMAT.momentFormat));
  const [getItemCheck, setItemCheck] = useState<boolean[]>([false, false, false]);

  const setTime: () => void = async () => {
    try {
      const result = await getPlayTime();
      if (result) {
        setPlayTimeData(result.data);
        setTimePercent(milliSecondsToNumber(result.data.milliSeconds));
        setProgressWidth({width: 100 <= milliSecondsToNumber(result.data.milliSeconds) ? '100%' : milliSecondsToNumber(result.data.milliSeconds) + '%'});
        setRealTime(moment().format(FORMAT.momentFormat));
        setItemCheck((state) =>
          state.map((bool, index) =>
            result.data.items[index] ? true : false
          ));

      }
    } catch (e) {
      console.log(e);
      errorStore.setState({isError: true});
    }
  }

  useEffect(() => {
    if (isAuth) {
      setTime();
    }
  }, [isAuth])

  return (
    <section className={styles.playTime}>
      <PlayTimeTitle/>
      <PlayTimeBar
        time={playTimeData.time}
        timePercent={timePercent}
        progressWidth={progressWidth}
        realTime={realTime}
        setTime={setTime}
      />
      <div className={styles.itemWrap}>
        {
          playGift.map((gift, index) =>
            <PlayTimeItems
              key={gift.id}
              {...gift}
              check={getItemCheck[index]}
              milliSeconds={playTimeData.milliSeconds}
              setItemCheck={setItemCheck}
            />)
        }
      </div>
      <LoadingModal/>
      <ErrorModal/>
    </section>
  )
}

export default PlayTime;