import * as React from 'react';
import styles from './playTimeItems.module.scss';
import {useEffect, useRef, useState} from "react";
import auth from "../../httpModules/auth";
import TextModal from "../textModal";
import {PlayTimeToPlayTimeItemsInterface} from "../../interfaces/props.interface";
import {hasParamLoadingManage} from "../../uitls/loadingManage";
import apiHttp from "../../httpModules/apiHttp";
import {RewardBodyInterface, RewardItemInterface} from "../../interfaces/reward.interface";
import {errorStore} from "../../store/errorStore";
import MILLISECOND from "../../consts/timeUint";

const PlayTimeItems = ({...gift}: PlayTimeToPlayTimeItemsInterface) => {
  const {title, img, time, milliSeconds, check, idx, setItemCheck} = gift;
  const isAuth = auth.isAuth;
  const ButtonRef = useRef<HTMLButtonElement>(null);
  const [open, setOpen] = useState(false);
  let giftImg = require(`../../assets/images/event1/${img}.png`);
  const getPlayTime = hasParamLoadingManage<RewardItemInterface, RewardBodyInterface>(apiHttp.postReward, {
    uuid: "742500ed-4d42-5dba-88bf-9a7047c84ddb",
    id: gift.itemId
  });

  const GetItemHandler = async () => {
    if (!check) {
      try {
        const result = await getPlayTime();
        if (result) {
          setItemCheck(state => state.map((el, index) => idx === index ? !el : el));
          if (ButtonRef.current) {
            ButtonRef.current.disabled = true;
            setOpen(true);
          }
        }
      } catch (e) {
        console.log(e);
        errorStore.setState({isError: true});
      }
    }
  }

  const LoginPage = () => {
    window.location.href = 'https://auth.riotgames.com/login#client_id=prod-xsso-leagueoflegends&code_challenge=veYvMOAaU5EeN9HFgpYtGN5PCP9MXByDahmfHhbfDuE&code_challenge_method=S256&redirect_uri=https%3A%2F%2Fxsso.leagueoflegends.com%2Fredirect&response_type=code&scope=openid%20account&state=4c9fa2af71dd0f5c4869438fdb'
  }

  const getClassName = () => {
    const tm = milliSeconds / MILLISECOND.oneHour;
    console.log(tm)
    if (isAuth) {
      if (check) {
        return styles.getActive;
      } else {
        if (time <= tm) return styles.Active;
        return styles.inActive;
      }
      return styles.inActive;
    } else {
      return styles.NotLogin;
    }
  }
  return (
    <article className={
      getClassName()
    }>
      <div className={styles.contentBox}>
        <p className={styles.TimeNotice}>{time}시간플레이 시</p>
        <div className={styles.itemImg}>
          <img src={giftImg} alt={title}/>
          <span className={styles.lines}></span>
        </div>
        <p>
          <span>전원증정</span>
          {title}
        </p>
      </div>
      {isAuth ?
        <button onClick={GetItemHandler} ref={ButtonRef}>{check ? '획득완료' : '획득하기'}</button> :
        <button onClick={LoginPage} ref={ButtonRef} className={styles.LoginButton}>로그인하여 획득</button>
      }
      <TextModal open={open} setOpen={setOpen}/>
    </article>
  )
}

export default PlayTimeItems