import * as React from 'react';
import styles from './playTimeBar.module.scss';
import reload from '../../assets/images/event1/reload.svg'
import Subtraction from '../../assets/images/event1/Subtraction.svg'
import ActiveSubtraction from '../../assets/images/event1/ActiveSubtraction.svg'
import {useEffect, useRef} from "react";
import {PlayTimeToPlayTimeBarInterface} from "../../interfaces/props.interface";

function PlayTimeBar({time, timePercent, progressWidth, realTime, setTime}: PlayTimeToPlayTimeBarInterface) {
  const PCTimeRef = useRef<HTMLDivElement>(null);
  const boxRef = useRef<HTMLDivElement>(null);
  const listRef = useRef<HTMLUListElement>(null);

  const BoxSize = () => {
    if (boxRef.current) {
      return boxRef.current.offsetWidth + 'px'
    }
  }

  const TimeBannerPositionChange = () => {
    if (PCTimeRef.current) {
      PCTimeRef.current.style.left = `calc(100% - ${BoxSize()}/2)`;
    }
  }


  const ListEl = () => {
    if (listRef.current) {
      let li = listRef.current.children;
      for (let i = 1; i < li.length; i++) {
        let setList = Number((li[i] as HTMLElement).dataset.num);
        let span = (li[i].children[0] as HTMLSpanElement);
        span.style.backgroundImage = `url('${Subtraction}')`;
        if (setList <= timePercent) {
          span.style.backgroundImage = `url('${ActiveSubtraction}')`;
        }
      }
    }
  }

  useEffect(() => {
    TimeBannerPositionChange()
    ListEl()
  }, [timePercent])

  return (
    <div className={styles.BarWrap}>
      <div className={styles.PlayTimeTop}>
        <h3><span>플레이어</span>님의 플레이시간은?</h3>
        <div className={styles.reload}>
          <a onClick={setTime}><img src={reload} alt="새로고침"/>{realTime} 기준</a>
        </div>
        <div className={styles.ReFreshNotice}>
          게임 이력은 최대 2분 정도 차이가 발생할 수 있습니다.<br/>
          게임 이력이 반영되지 않을 경우<br className={styles.TabletMobileDisplay}/> 버튼을 누르거나 새로고침을 해주세요.
        </div>
      </div>

      <div className={styles.ProgressContainer}>
        <div className={styles.defaultBar}>
          <div className={styles.ProgressBar} style={progressWidth}>
            <div className={styles.PCTime} ref={PCTimeRef}>
              <span className={styles.PcTimeItem} ref={boxRef}>{time}</span>
            </div>
          </div>
        </div>
      </div>
      <ul className={styles.hours} ref={listRef}>
        <li data-num={0}><span></span>0</li>
        <li data-num={20}><span></span>1시간</li>
        <li data-num={40} className={styles.PCDisplay}><span></span>2시간</li>
        <li data-num={60}><span></span>3시간</li>
        <li data-num={80} className={styles.PCDisplay}><span></span>4시간</li>
        <li data-num={100}><span></span>5시간</li>
      </ul>
    </div>
  )
}

export default PlayTimeBar;