import * as React from 'react';
import styles from './weekList.module.scss';
import {WeekSkins} from '../../interfaces/weekSkins'


function WeekList({...item}: WeekSkins) {
  const {no, weekly, name, src, msrc} = item;
  const imgSrc = require(`../../assets/images/event2/${src}.png`);
  const mImgSrc = require(`../../assets/images/event2/${msrc}.png`);

  return (
    <article className={styles.WeekList}>
      <h4>{no}주차무료체험</h4>
      <p className={styles.WeekDay}>{weekly}</p>
      <img src={imgSrc} alt={name} className={styles.PCDisplay}/>
      <img src={mImgSrc} alt={name} className={styles.TabletMobileDisplay}/>
    </article>
  )
}

export default WeekList