import styles from "../../styles/eventContentTitle.module.scss";

function PlayTimeTitle() {
  return (
    <div className={styles.eventTitle}>
      <div className={styles.eventBanner}>EVENT 01</div>
      <p className={styles.eventTopText}>별 수호자가 가져온 반짝반짝한 선물!</p>
      <h2>PC방에서 플레이하면 <br/>
        <span>챔피언, 스킨, 신화정수</span>가<br className={styles.mobileDisplay}/> 펑펑!
      </h2>
      <p className={styles.eventBottomText}>이벤트 기간 동안 <span>PC방에서 플레이</span>하고 선물을 받으세요.</p>
    </div>
  )
}

export default PlayTimeTitle;