import * as React from 'react';
import styles from './banner.module.scss';

function Banner() {

  return (
    <section className={styles.bannerWrap}>
      <h1 className={styles.logo}></h1>
      <div className={styles.content}>
        <span className={styles.period}>2022/07/01 10:00 - 2022/07/31 23:59</span>
        <p className={styles.point}>별 수호자의 귀환을 환영하는
          <br className={styles.TabletMobileDisplay}/> PC방의 특별한 혜택!</p>
        <p>무작위 챔피언, 무작위 스킨, 신화 정수를 선물받고 <br/>
          매주 달라지는 별 수호자 스킨도 무료로 체험하세요.</p>
      </div>
    </section>
  )
}

export default Banner;