import * as React from 'react';
import styles from "../../styles/eventContentTitle.module.scss";


function WeekSkinTitle() {
  return (
    <div className={styles.eventTitle}>
      <div className={styles.eventBanner}>EVENT 02</div>
      <p className={styles.eventTopText}>별 수호자, PC방 강림</p>
      <h2>별 수호자 스킨을 <br/>
        <span>PC방에서</span> 만나보세요!
      </h2>
    </div>
  )
}

export default WeekSkinTitle;