import axios, {AxiosError, AxiosInstance, AxiosResponse} from "axios";
import AxiosInterceptorResponse from "../interfaces/httpInterfaces/AxiosInterceptorResponse";
import ResponseBase from "../interfaces/httpInterfaces/responseBase";
import STATUS from "../consts/status";

class Http {
  protected axios: AxiosInstance;
  //protected willUseDummyData: boolean;
  protected urlPath: string;

  constructor() {
    //this.willUseDummyData = process.env.REACT_APP_PUBLIC_WILL_USE_DUMMY_DATA;
    this.urlPath = process.env.REACT_APP_DEV ? "/dev" : "/prod";
    this.axios = Http.createAxios();
    const transformResponse: AxiosInterceptorResponse = Http.transformResponse();
    this.axios.interceptors.response.use(transformResponse.onFulfilled, transformResponse.onRejected);
  }

  private static createAxios(): AxiosInstance {
    return axios.create({
      baseURL: process.env.REACT_APP_PUBLIC_API_URL,
      withCredentials: false,
      timeout: 1000 * 5,
      headers: {
        "Content-Type": "application/json",
        //"api-key": process.env.REACT_APP_PUBLIC_API_KEY
      }
    });
  }


  private static transformResponse(): AxiosInterceptorResponse {
    return {
      onFulfilled: (response: AxiosResponse): AxiosResponse => {
        response.data = {
          statusCode: response.status,
          jsonResult: response.status === STATUS.OK || response.status === STATUS.CREATED ? response.data : null
        };
        return response;
      },
      onRejected: (error: AxiosError): Promise<AxiosError> => {
        console.log("error..");
        return Promise.reject(error);
      }
    };
  }

  protected respondError<T>(error: unknown): Promise<ResponseBase<T>> {
    console.error(error);
    let statusCode: number | undefined;
    if (axios.isAxiosError(error)) statusCode = (error as AxiosError).response?.status;
    return new Promise((resolve, reject) => {
      resolve({
        statusCode: statusCode ?? STATUS.INTERNAL_SERVER_ERROR,
        jsonResult: null
      });
    });
  }
}

export default Http;
