import React from 'react';

import "../styles/common.module.scss";
import './app.scss';
import Banner from "../components/banner";
import PlayTime from "../components/playTime";
import WeekSkin from "../components/weekSkin";
import Precautions from "../components/bottomPrecautions";

function App() {
    return (
        <div className="app">
            <div className="bg_top"></div>
            <div className="container">
                <Banner/>
                <main>
                    <PlayTime/>
                    <WeekSkin/>
                </main>
            </div>
            <Precautions/>
        </div>
    );
}

export default App;
