import * as React from 'react';
import styles from '../textModal/modal.module.scss';
import {Modal, Button, Box} from "@mui/material";
import {errorStore} from "../../store/errorStore";


function ErrorModal() {
  const open = errorStore((state) => state.isError);
  const message = errorStore((state) => state.errorMessage);
  const handleLink = () => window.location.href = 'https://support-leagueoflegends.riotgames.com/hc/ko';
  const handleClose = () => errorStore.setState({isError: false});
  return (
    <Modal
      open={open}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box className={styles.modalWrap}>
        <p id="modal-modal-description" className={styles.errorModal}>
          <span>오류가 발생하였습니다.</span>
          다시 시도해주세요. 오류가 지속 발생할 경우 <br /> <strong>라이엇 고객지원</strong>으로 연락주세요.

          {/*   {message}*/}
        </p>
        <div className={styles.buttonWrap}>
          <Button onClick={handleClose}>확인</Button>
          <Button sx={{backgroundColor: ""}} onClick={handleLink}>고객지원</Button>
        </div>
      </Box>
    </Modal>
  );
}


export default ErrorModal;