import * as React from 'react';
import styles from './modal.module.scss';
import {Modal, Button, Box} from "@mui/material";
import {ModalType} from "../../interfaces/ModalType";


function TextModal({open, setOpen}: ModalType) {
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        onClick={handleClose}
      >
        <Box className={styles.modalWrap}>

          <p id="modal-modal-description">
            <span>보상을 획득하였습니다.</span>
            게임 내의 <strong>마법 공학 제작소</strong>에서<br className={styles.TabletMobileDisplay}/> 확인하세요.
          </p>
          <div className={styles.buttonWrap}>
            <Button onClick={handleClose}>확인</Button>
          </div>
        </Box>
      </Modal>
    </div>
  );
}

export default TextModal;