import jsonResultInterface from "../interfaces/httpInterfaces/jsonRsult.interface";
import {loadingStore} from "../store/loadingStore";
import ResponseBase from "../interfaces/httpInterfaces/responseBase";
import {ErrorInterface} from "../interfaces/error.interface";
import {errorStore} from "../store/errorStore";


const hasParamLoadingManage = <T, J>(action: (ap: J) => Promise<ResponseBase<T>>, p: J): () => Promise<jsonResultInterface<T> | null> => {

  const apiCall = async () => {
    loadingStore.setState({loading: true});
    try {
      const data = await action(p);
      const {jsonResult} = data
      if (jsonResult) {
        if (jsonResult.status === 200 || jsonResult.status === 201) return jsonResult;
        errorStore.setState({isError: true, errorMessage: jsonResult.message, errorCode: jsonResult.status});
      }
      return null;
    } catch (e) {
      errorStore.setState({isError: true, errorMessage: "error", errorCode: 500});
      return null;
    } finally {
      setTimeout(function () {
        loadingStore.setState({loading: false});
      }, 300)
    }

  }

  return apiCall;
}

const loadingManage = <T>(action: () => Promise<ResponseBase<T>>): () => Promise<jsonResultInterface<T> | null> => {
  const apiCall = async () => {
    console.log("data loading...");
    loadingStore.setState({loading: true});
    try {
      const {jsonResult} = await action();
      if (jsonResult?.status === 200 || jsonResult?.status === 201) {
        return jsonResult;
      } else {
        return null
      }
    } catch (e) {
      console.log(e);
      return null;
    } finally {
      loadingStore.setState({loading: false});
      console.log("get data...");
    }

  }

  return apiCall;
}

export {loadingManage, hasParamLoadingManage};